.sidenav {
  width: 310px;
  overflow: auto;
}
.sidenav > li:last-child {
  margin-bottom: 100px;
}

#main {
  padding-left: 310px;
}

@media only screen and (max-width: 992px) {
  #main {
    padding-left: 0;
  }

  .sidenav-trigger {
    display: block !important;
  }
}
.icon-bar {
  margin-top: 2em;
  margin-bottom: 3em;
  overflow: auto;
}
.icon-bar a {
  float: left;
  margin-top: 0;
  margin-bottom: 0;
  width: 18%;
  margin-left: 1%;
  margin-right: 1%;
  transition: all 0.3s ease;
  color: black;
}
.icon-bar a i {
  font-size: 2rem;
}
.icon-bar a:first-child {
  margin-left: 0;
}
.icon-bar a:last-child {
  margin-right: 0;
}
.icon-bar a:hover {
  color: blue;
}

/*# sourceMappingURL=Sidenav.css.map */

.timeline {
  position: relative;
}
.timeline .timeline-event {
  position: relative;
  padding-top: 5px;
  padding-bottom: 5px;
}
.timeline .timeline-event:first-child {
  margin-top: 20px;
}
.timeline .timeline-event:first-child::before {
  display: none;
}
.timeline .timeline-event:not(:first-child) {
  margin-top: -60px;
}
.timeline .timeline-event:last-child::after {
  display: none;
}
.timeline .timeline-event:nth-child(even) .timeline-content {
  margin-left: calc(50% + 50px);
}
.timeline .timeline-event:nth-child(odd) .timeline-content {
  margin-left: 0;
}
.timeline .timeline-event .timeline-content {
  position: relative;
  width: calc(50% - 50px);
}
.timeline .timeline-event::before {
  display: block;
  content: "";
  width: 2px;
  height: calc(50% - 30px);
  position: absolute;
  background: #d2d2d2;
  left: calc(50% - 1px);
  top: 0;
}
.timeline .timeline-event::after {
  display: block;
  content: "";
  width: 2px;
  height: calc(50% - 30px);
  position: absolute;
  background: #d2d2d2;
  left: calc(50% - 1px);
  top: calc(50% + 30px);
}
.timeline .timeline-badge {
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  background: #d2d2d2;
  top: calc(50% - 20px);
  right: calc(50% - 20px);
  border-radius: 50%;
  text-align: center;
  cursor: default;
}
.timeline i {
  font-size: 25px;
  line-height: 40px;
}

@media only screen and (max-width: 600px) {
  .timeline .timeline-event:not(:first-child) {
    margin-top: 0px;
  }
  .timeline .timeline-event:nth-child(n) .timeline-content {
    width: calc(100% - 70px);
    margin-left: 70px;
  }
  .timeline .timeline-event:nth-child(n)::before, .timeline .timeline-event:nth-child(n)::after {
    left: 19px;
  }
  .timeline .timeline-badge {
    left: 0;
  }
}

/*# sourceMappingURL=Timeline.css.map */

